.player-area {
  -webkit-box-shadow: 2px 2px 5px black;
          box-shadow: 2px 2px 5px black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-height: calc(0.3*88mm);
  width: 100%;
}
.player-area .income, .player-area .money,  .player-area> .status > .name {
  background-color: green;
  -webkit-box-shadow: 2px 2px 5px black;
          box-shadow: 2px 2px 5px black;
  color: white;
  font-family: monospace;
  padding: 2px;
}
.player-area> .status{
    min-width: 150px;
}
.player-area .money {
  background-color: darkgreen;
}
.player-area > .status > .name {
  background-color: darkorange;
  padding: 4px;
  text-align: center;
}
.player-area .cards {
  display: -ms-flexbox;
  display: flex;
}



