.game-area {
  border: 1px solid black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-flow: column;
      flex-flow: column;
  padding: 1em;
}
.game-area .player-areas {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.game-area .card-offer {
  display: -ms-flexbox;
  display: flex;
}
.game-area > .status {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.game-area > .status > div {
  -ms-flex-positive: 1;
      flex-grow: 1;
}



