.card, .loan-offer, .deck, .discard {
  background-color: tan;
  border-radius: 10%;
  -webkit-box-shadow: 2px 2px 5px black;
          box-shadow: 2px 2px 5px black;
  height: calc(0.3*(88mm - 6mm));
  width: calc(0.3*(63mm - 6mm));
  padding: calc(0.3*3mm);
  margin: 3px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.card.selected {
  border: 1px solid red;
  -webkit-box-shadow: 2px 2px 5px red;
          box-shadow: 2px 2px 5px red;
}
.building .card {
  border: 1px solid green;
  -webkit-box-shadow: 2px 2px 5px green;
          box-shadow: 2px 2px 5px green;
}
.card .name {
  font-size: 0.5em;
}
.card .cost {
  font-size: 0.6em;
  float: left;
  -webkit-transform: scale(0.6);
      -ms-transform: scale(0.6);
          transform: scale(0.6);
  -webkit-transform-origin: left top;
      -ms-transform-origin: left top;
          transform-origin: left top;
  padding: 0.9mm;
  background-color: tan;
  border-radius: 0.9mm;
}

.card .provides {
  display: -ms-flexbox;
  display: flex;
  font-size: 0.8em;
}
.card > .art{
  height: 58%;
  background-color: white;
  margin: 0.2mm;
  border-radius: 0.8mm;
}

.loan-offer {
    background-color: red;
    color: white;
    font-size: 0.6em;
}
.deck, .discard {
    background-color: darkgreen;
    color: white;
    font-size: 1.5em;
    line-height: 80px;
    vertical-align: middle;
}
.discard {
    background-color: darkgrey;
}
.icon {
  background-size: contain;
  font-size: 0;
  height: 5mm;
  width: 5mm;
  margin: 0.3mm;
}

.icon._income{
  background-image: url('img/cash.png');
}
.icon._coffee{
  background-image: url('img/coffee-beans.png');
}
.icon._wood{
  background-image: url('img/log.png');
}
.icon._stone{
  background-image: url('img/stone-tablet.png');
}
.icon._food{
  background-image: url('img/sliced-bread.png');
}

.icon._1, .icon._2, .icon._3, .icon._4,
.icon._5, .icon._6, .icon._7, .icon._8 {
  font-size: 5mm;
  line-height: 5mm;
}



